import React, { useState } from 'react';
import axios from 'axios';
import moment from 'moment';
import DatePicker from 'react-datepicker';
import "react-datepicker/dist/react-datepicker.css";
// import Store from '../store';

export default () => {
  const [searchTerm, setSearchTermState] = useState('');
  const [showSuggestions, toggleSuggestions] = useState(false);
  const [showFilter, toggleFilter] = useState(false);
  const [suggestions, setSuggestions] = useState([{}]);

  const [filters, setFilters] = useState({startDate: new Date(), endDate: new Date()});

  const search = async() => {
    const body = {
      term: searchTerm,
    };
    showFilter ? body.filters = filters : body.filters = null;

    const result = searchTerm ? await axios.post('http://localhost:3000/search', body).then(response => response.data): '';

    if (result) {
      setSuggestions(result.data);
    }
    searchTerm && searchTerm.length > 1 ? toggleSuggestions(true) : toggleSuggestions(false);
  }
  const searchTermChanged = ({ target: { value: term }}) => {
    setSearchTermState(term);
    search();
  }
  const displayResult = item => {
    alert(JSON.stringify(item));
  };

  const startDateChanged = value => {
    filters.startDate = value;
    setFilters(filters);
  }

  const endDateChanged = value => {
    filters.endDate = value;
    setFilters(filters);
  }
	return (
    <div>
			<div className="container" id="search-container">
				<div className="row">
					<input type="text" placeholder="Type anything..." className="form-control col-sm-12" name="username" id="username" value={searchTerm} onChange={searchTermChanged}/>
				</div>
        <div className="form-check">
          <input type="checkbox" className="form-check-input" id="enableFilter" checked={showFilter} onChange={(event) => {
            toggleFilter(event.target.checked);
            search();
          }} />
          <label className="form-check-label" htmlFor="enableFilter">Filters</label>
        </div>
        {
          showFilter ? (<div className="filter-group">
          <label>Move in
            <DatePicker name="startDate" selected={filters.startDate} dateFormat="yyyy/MM/dd" onChange={startDateChanged}/>
          </label>
          <label>Move out
            <DatePicker name="startDate" selected={filters.endDate} dateFormat="yyyy/MM/dd" onChange={endDateChanged}/>
          </label>

      </div>) : ''
        }
			</div>
			{
        showSuggestions ? (<div className="container" id="suggestion-container">
				<div className="row">
          <div className="col-sm-12">
            <ul>
              {
                suggestions.map(item =>
                <li key={item._id} onClick={() => displayResult(item)}>
                  <div>
                    Code: <span>{item.code}</span><br/>
                    Booker: {item.booker && item.booker.profile &&
                      <span>
                        <span>{`${item.booker.profile.firstName} ${item.booker.profile.lastName}`}</span>
                        <span className="emailAddress">({`${item.booker.emails && item.booker.emails[0].address}`})</span>
                        </span>
                      }<br/>
                    Dates: <span>{moment(item.startDate).format('YYYY-MM-DD')} - {moment(item.endDate).format('YYYY-MM-DD')} </span>
                  </div>
                  <hr/>
                </li>)
              }
            </ul>
          </div>
				</div>
			</div>) : ''
      }
		</div>
	);
}
